:root {
  --main-font: "Lucida Console", "Courier New", monospace;
  --bg-color: #000000;
  --accent-color: #ffeb3b;
  --text-color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;


}


html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: var(--main-font);
  background: var(--bg-color);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}


.app {
  text-align: center;
  width: 100%;
  overflow-x: hidden;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 5vh;
  background: var(--bg-color);
  color: var(--text-color);
  z-index: 1000;
  display: flex;
  align-items: center;
}

.header-logo {
  color: var(--accent-color);
  font-weight: bold;
  font-size: 1.3rem;
  text-decoration: none;
  margin-left: 2rem;
  margin-right: auto;
  letter-spacing: 1px;
  transition: opacity 0.2s;
}

.header-logo:hover {
  opacity: 0.7;
}

nav {
  display: flex;
  gap: 2rem;
  padding: 0 2rem;
}

.nav-link {
  color: var(--text-color);
  text-decoration: none;
  font-weight: 600;
  transition: transform 0.3s;
}

.nav-link:hover {
  transform: translateY(-2px);
}

main {
  padding-top: 5vh;
}

.page-container {
  width: 100%;
}

.intro-section {
  width: 100%;
  min-height: 95vh;
  background-color: var(--accent-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6vh 0 15vh 0;
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.logo-container img {
  height: 40vh;
  width: auto;
}

.contact-button {
  background-color: var(--accent-color);
  color: var(--bg-color);
  font-family: var(--main-font);
  font-size: 2rem;
  font-weight: 600;
  border: 3px solid var(--bg-color);
  border-radius: 100px;
  padding: 1.2rem 2.5rem;
  text-decoration: none;
  transition: transform 0.2s;
  display: inline-block;
}

.contact-button:hover {
  transform: translateY(-2px);
}

.about-section {
  background: var(--bg-color);
}

.info-section {
  width: 100%;
  min-height: 75vh;
  background: var(--bg-color);
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15vh 2rem 2rem 2rem;
  text-align: center;
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94), 
              transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.info-section.animate-in {
  opacity: 1;
  transform: translateY(0);
}

.info-section#contact {
  opacity: 1;
  transform: translateY(0);
  transition: none;
}

.info-question {
  color: var(--accent-color);
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  letter-spacing: 0.5px;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s;
}

.info-answer {
  font-size: 1.5rem;
  max-width: 800px;
  line-height: 1.7;
  font-weight: 500;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
}

.info-section.animate-in .info-question,
.info-section.animate-in .info-answer {
  opacity: 1;
  transform: translateY(0);
}

.info-section#contact .info-question,
.info-section#contact .info-answer {
  opacity: 1;
  transform: translateY(0);
  transition: none;
}

.info-section:nth-child(even) {
  transform: translateY(40px);
}

.info-section:nth-child(odd) {
  transform: translateY(60px);
}

.info-section.animate-in:nth-child(even),
.info-section.animate-in:nth-child(odd) {
  transform: translateY(0);
}

.info-section:nth-child(2) .info-question,
.info-section:nth-child(2) .info-answer {
  transition-delay: 0.1s, 0.3s;
}

.info-section:nth-child(3) .info-question,
.info-section:nth-child(3) .info-answer {
  transition-delay: 0.15s, 0.35s;
}

.info-section:nth-child(4) .info-question,
.info-section:nth-child(4) .info-answer {
  transition-delay: 0.2s, 0.4s;
}

.info-section:nth-child(5) .info-question,
.info-section:nth-child(5) .info-answer {
  transition-delay: 0.25s, 0.45s;
}

.info-section#contact {
  transform: translateY(0) !important;
}

.contact-content {
  display: flex;
  justify-content: center;
  width: 100%;
}

.contact-form {
  width: 100%;
  max-width: 700px;
  background-color: #111;
  border-radius: 20px;
  border: 3px solid var(--accent-color);
  padding: 2rem;
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  color: var(--accent-color);
  font-family: var(--main-font);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.form-input,
.form-textarea {
  background-color: #222;
  border: 2px solid #444;
  border-radius: 8px;
  color: var(--text-color);
  font-family: var(--main-font);
  font-size: 1rem;
  padding: 1rem;
  width: 100%;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px rgba(255, 235, 59, 0.2);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: #888;
  font-family: var(--main-font);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-button {
  background-color: var(--accent-color);
  color: var(--bg-color);
  font-family: var(--main-font);
  font-size: 1.3rem;
  font-weight: 600;
  border: 3px solid var(--bg-color);
  border-radius: 50px;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  align-self: center;
  min-width: 200px;
}

.submit-button:hover {
  transform: translateY(-2px);
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.submit-message {
  text-align: center;
  margin-top: 1rem;
  font-family: var(--main-font);
  font-size: 1.1rem;
}

.submit-message.success {
  color: #4CAF50;
}

.submit-message.error {
  color: #f44336;
}

.footer-section {
  width: 100%;
  background: var(--bg-color);
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  min-height: 40vh;
  text-align: center;
}

.footer-main {
  margin-bottom: 2rem;
  transition: transform 0.2s;
}

.footer-main:hover {
  transform: translateY(-2px);
}

.footer-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-logo {
  height: 250px;
  width: auto;
}

.footer-link {
  color: var(--accent-color);
  text-decoration: none;
  font-size: 3rem;
  font-family: 'Lucida Console', Monaco, monospace;
}

.footer-copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.footer-copy div {
  font-size: 1.2rem;
  font-family: 'Lucida Console', Monaco, monospace;
}

.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  color: var(--accent-color);
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  margin-right: 1rem;
}

.mobile-menu {
  position: absolute;
  top: 100%;
  right: 0;
  width: 200px;
  background: var(--bg-color);
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  z-index: 1001;
  opacity: 0;
  transform: translateY(-10px);
  visibility: hidden;
  transition: opacity 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0.3s;
}

.mobile-menu.open {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

.mobile-nav {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}

.mobile-nav .nav-link {
  padding: 0.8rem 1.5rem;
  border-bottom: 1px solid #333;
  transition: background-color 0.2s ease;
  opacity: 0;
  transform: translateX(-10px);
}

.mobile-nav .nav-link:last-child {
  border-bottom: none;
}

.mobile-nav .nav-link:hover {
  background-color: #222;
  transform: translateX(-10px);
}

.mobile-menu.open .mobile-nav .nav-link {
  animation: slideInLeft 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

.mobile-menu.open .mobile-nav .nav-link:nth-child(1) {
  animation-delay: 0.1s;
}

.mobile-menu.open .mobile-nav .nav-link:nth-child(2) {
  animation-delay: 0.15s;
}

.mobile-menu.open .mobile-nav .nav-link:nth-child(3) {
  animation-delay: 0.2s;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 768px) {
  .footer-link {
    font-size: 2rem;
  }
  
  .footer-copy div {
    font-size: 0.9rem;
  }

  .logo-container img {
    height: 25vh;
  }
  
  .intro-section {
    padding: 6vh 0 10vh 0;
  }
  
  .contact-button {
    font-size: 1.5rem;
    padding: 1rem 2rem;
  }

  .section-content {
    flex-direction: column;
    gap: 2rem;
  }
  
  .text-content,
  .team-content {
    transform: translateY(30px);
  }
  
  .info-section.animate-in .text-content,
  .info-section.animate-in .team-content {
    transform: translateY(0);
  }
  
  .team-links {
    grid-template-columns: 1fr;
    gap: 0.8rem;
  }
  
  .team-link {
    font-size: 0.9rem;
    padding: 0.6rem 1rem;
  }
}

@media (max-width: 480px) {
  .footer-link {
    font-size: 1.5rem;
  }
  
  .footer-copy div {
    font-size: 0.7rem;
  }

  .logo-container img {
    height: 20vh;
  }
  
  .contact-button {
    font-size: 1.3rem;
    padding: 0.8rem 1.5rem;
  }

  .team-photo {
    max-width: 300px;
  }
  
  .team-link {
    font-size: 0.8rem;
    padding: 0.5rem 0.8rem;
  }
}

@media (max-width: 700px) {
  header {
    height: 7vh;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    position: fixed;
  }

  main {
    padding-top: 7vh;
  }

  .header-logo {
    font-size: 1.2rem;
    margin-left: 1rem;
    margin-bottom: 0;
    margin-right: auto;
    display: flex;
    align-items: center;
    height: 100%;
  }

  nav {
    display: none;
  }

  .mobile-menu-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: 1rem;
    font-size: 2rem;
    padding: 0;
    height: 100%;
    width: 3rem;
    background: none;
    border: none;
    color: var(--accent-color);
    cursor: pointer;
    transition: transform 0.2s ease;
  }

  .mobile-menu-toggle:hover {
    transform: scale(1.1);
  }

  .mobile-menu {
    right: 1rem;
    left: auto;
    top: 7vh;
  }

  .info-section {
    min-height: 50vh;
    padding: 0 4vw;
    transform: translateY(30px) !important;
  }

  .info-section:nth-child(even),
  .info-section:nth-child(odd) {
    transform: translateY(30px) !important;
  }

  .info-section.animate-in:nth-child(even),
  .info-section.animate-in:nth-child(odd) {
    transform: translateY(0) !important;
  }

  .logo-container img {
    height: 20vh !important;
    max-width: 80vw;
    width: auto;
  }

  .footer-logo {
    max-width: 80vw;
    height: auto;
    max-height: 30vh;
  }

  .info-question {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: left;
  }

  .info-answer {
    font-size: 1rem;
    max-width: 95vw;
    text-align: left;
  }

  .contact-button {
    font-size: 1.1rem !important;
    padding: 0.7rem 1.2rem !important;
    min-width: 160px;
    border-width: 3px;
    border-radius: 50px;
    margin: 2rem auto 0 auto;
  }

  .contact-form {
    max-width: 95vw;
    padding: 2rem 1.5rem;
    border-radius: 15px;
    margin: 1rem auto;
  }

  .form-label {
    font-size: 1rem;
  }

  .form-input,
  .form-textarea {
    font-size: 0.9rem;
    padding: 0.8rem;
  }

  .submit-button {
    font-size: 1.1rem;
    padding: 0.8rem 1.5rem;
    min-width: 180px;
  }

  .footer-main {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0 0 0;
    gap: 2rem;
  }

  .footer-copy {
    font-size: 0.9rem;
    padding: 1rem 0 2rem 0;
  }
}

.section-content {
  display: flex;
  align-items: center;
  gap: 4rem;
  width: 100%;
  max-width: 1200px;
}

.text-content {
  flex: 1;
  opacity: 0;
  transform: translateX(-30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s;
}

.team-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  opacity: 0;
  transform: translateX(30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
}

.team-photo {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}



.team-links {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  width: 100%;
  max-width: 400px;
}

.team-link {
  color: var(--accent-color);
  text-decoration: none;
  font-family: 'Lucida Console', Monaco, monospace;
  font-size: 1rem;
  padding: 0.8rem 1.2rem;
  border: 2px solid var(--accent-color);
  border-radius: 8px;
  text-align: center;
  transition: all 0.3s ease;
  background: transparent;
}

.team-link:hover {
  background: var(--accent-color);
  color: var(--bg-color);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 223, 0, 0.3);
}

.info-section.animate-in .text-content,
.info-section.animate-in .team-content {
  opacity: 1;
  transform: translateX(0);
}

.info-section.animate-in .info-question,
.info-section.animate-in .info-answer {
  opacity: 1;
  transform: translateY(0);
}

